import React from "react";

import home_image from "../assets/home_image.png";

class Home extends React.Component {
  render() {
    return (
      <div className="container">
        <div className="row align-items-center h-100">
          <div className="col-12 col-md-6 mt-5 mt-md-0">
            <div className="text-center">
              <h1 style={{ fontWeight: "bold", fontSize: "60px" }}>Torre</h1>
              <h1 style={{ fontWeight: "bold", fontSize: "60px" }}>Salary</h1>
            </div>

            <p className="text-torre mt-5" style={{ fontSize: "20px" }}>
              Calculate the <span className="text-bold">salary</span> you can
              aspire to, based on your{" "}
              <span className="text-bold">skills and experience</span>
            </p>

            <form className="mt-3">
              <input
                type="text"
                className="form-control input-torre"
                id="txtUserName"
                placeholder="Enter your Torre user or Genome Link"
              />
              <button className="btn btn-torre form-control mt-3">
                START NOW
              </button>
            </form>
            <div className="text-center mt-3 mb-3">OR</div>
            <div className="text-center">
              <a className="text-action-torre">GET MY GENOME</a>
            </div>
          </div>
          <div className="d-none d-md-block col-5 offset-1">
            <img
              className="d-block mx-auto"
              src={home_image}
              alt="men with money"
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
